<template>
    <section class="settings-wrapper">

        <!--<div class="row">
            <MDInput label="Название Компании" />
        </div>-->

        <div class="row">
            <input type="checkbox" id="settings-switch-1" name="set-name" class="switch-input" v-model="pricesCheckbox">

            <label class="switch-label" for="settings-switch-1">
                Цены:
                    <span class="toggle--on">Розничные</span>
                    <span class="toggle--off">Оптовые</span>
            </label>
        </div>

        <div class="row bottom">
            <MDRaisedButton text="Очистить Кеш" @click="cleanCache" />

            <MDRaisedButton text="Выйти из аккаунта" @click="logoutInit" />
        </div>


        <!--<div class="row bottom">
            <MDRaisedButton text="Сохранить" />
        </div>-->

    </section>
</template>

<script>
    import { mapActions, mapMutations, mapGetters} from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";

    import pricesTypes from "../store/modules/user/prices.type"

    import {Events} from "../common/events";

    //import MDInput from "./MD/MDInput";
    import MDRaisedButton from "./components/MD/MDRaisedButton";

    import myCacheStorage from "../common/cache"

    const apiStorage = myCacheStorage('api');
    const imagesStorage = myCacheStorage('images');
    const fullSizeImagesStorage = myCacheStorage('fullSizeImages');
    const catalogStorage = myCacheStorage('catalog');

    export default {
        name: "Setting",

        components: {MDRaisedButton},

        data: () => {return {
            pricesCheckbox: false,
        }},

        methods: {
            ...mapActions(modules.user, [
                actions.user.LOGOUT,
            ]),

            ...mapMutations(modules.user, [
                mutations.user.SET_PRICES_TYPE,
            ]),

            cleanCache() {
                if (apiStorage !== null && imagesStorage !== null && fullSizeImagesStorage !== null) {
                    apiStorage.clean();
                    imagesStorage.clean();
                    fullSizeImagesStorage.clean();
                    catalogStorage.clean();
                } else {
                    window.alert("one of caches is null");
                }
            },

            logoutInit() {
                this.logout()
                    .then((isLogout) => {
                        if (isLogout) {
                            this.$eventBus.$emit(Events.hideUnfocusedBackground);
                            this.$router.push({name: "login"})
                        }
                    });
            },
        },

        computed: {
            ...mapGetters(modules.user, [
                "pricesType"
            ])
        },

        watch:{
            pricesCheckbox: function(newVal) {
                if (newVal) {
                    this.setPricesType(pricesTypes.PRICE_RETAIL);
                } else {
                    this.setPricesType(pricesTypes.PRICE_WHOLESALE);
                }
            },
        },

        mounted() {
            this.pricesCheckbox = this.pricesType === pricesTypes.PRICE_RETAIL;
        },
    }
</script>

<style lang="sass" scoped>

    .settings-wrapper
        width: 90%
        max-width: 500px
        margin: auto
        font-family: Roboto, sans-serif

        .row
            display: block
            margin: 10px auto

            button
                display: inline-block
                margin-left: 10px
                margin-right: 10px

                &:first-child
                    margin-left: 0

            .switch-input
                display: none

            .switch-label
                position: relative
                display: inline-block
                min-width: 112px
                cursor: pointer
                font-weight: 500
                text-align: left
                margin: 16px
                padding: 16px 0 16px 44px

            .switch-label:before, .switch-label:after
                content: ""
                position: absolute
                margin: 0
                outline: 0
                top: 50%
                -ms-transform: translate(0, -50%)
                -webkit-transform: translate(0, -50%)
                transform: translate(0, -50%)
                -webkit-transition: all 0.3s ease
                transition: all 0.3s ease

            .switch-label:before
                left: 1px
                width: 34px
                height: 14px
                background-color: #9E9E9E
                border-radius: 8px

            .switch-label:after
                left: 0
                width: 20px
                height: 20px
                background-color: #FAFAFA
                border-radius: 50%
                box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.14), 0 2px 2px 0 rgba(0, 0, 0, 0.098), 0 1px 5px 0 rgba(0, 0, 0, 0.084)

            .switch-label .toggle--on
                display: none

            .switch-label .toggle--off
                display: inline-block

            .switch-input:checked + .switch-label:before
                background-color: #1d5db5c9

            .switch-input:checked + .switch-label:after
                background-color: #2f65af
                -ms-transform: translate(80%, -50%)
                -webkit-transform: translate(80%, -50%)
                transform: translate(80%, -50%)

            .switch-input:checked + .switch-label .toggle--on
                display: inline-block

            .switch-input:checked + .switch-label .toggle--off
                display: none

</style>